<template>
  <div>
    <rank-users-component v-for="rank in ranks" v-bind="rank" :key="rank.api" />
  </div>
</template>
<script>
  import RankUsersComponent from '../components/rankUsersComponent';
  const ranks = [
    {
      title: '',
      listUrl: '/rank/f/today',
      api: '/feeds/ranks?type=day&limit=15',
      name: 'rankFeedsToday'
    }, {
      title: '',
      listUrl: '/rank/f/week',
      api: '/feeds/ranks?type=week&limit=15',
      name: 'rankFeedsWeek'
    }, {
      title: '',
      listUrl: '/rank/f/month',
      api: '/feeds/ranks?type=month&limit=15',
      name: 'rankFeedsMonth'
    }
  ];

  export default {
    name: 'rankFeeds',
    components: {
      RankUsersComponent
    },
    data () {
      return {
        ranks
      }
    }
  }
</script>


